/* Page background */
body {
    background-color: #121212;
    font-family: Arial, sans-serif;
  }
  
  /* Centered container */
  .container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
    justify-items: center;
    background-image: url('../assets/lp-bg-17.jpg'); /*2, 10*/
    background-position: center;
    background-size: cover;
  }
  
  /* Square box with curved corners */
  .loginBox {
    background-color: #1c1c1e;
    padding: 40px;
    border: 1.5px solid rgb(167, 165, 165);
    border-radius: 15px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    justify-items: center;
    animation: fadeIn 1s ease-in-out;
    height: 700px;
    width: 700px;
  }
  
  /* Title styling */
  .title {
    color: rgb(5, 109, 255);
    font-size: 80px;
    margin-bottom: 20px;
  }
  
  /* Profile image */
  .userProfileImage {
    width: 300px;
    height: 300px;
    transition: transform 0.3s ease;
    margin-bottom: 20px;
    
  }
  .userProfileImage:hover {
    transform: scale(1.1);
}
  /* Login form */
  .loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .loginForm input {
    margin: 10px 0;
    padding: 12px;
    width: 100%;
    border-radius: 10px;
    border: 1px solid #333;
    background-color: #2c2c2e;
    color: #fff;
    transition: border-color 0.3s;
    font-size: 20px;
  }
  
  .loginForm input:focus {
    border-color: #4a90e2;
  }
  
  /* Button styling */
  .btnLink {
    width: 100%;
    text-decoration: none;
    margin-top: 15px;
  }
  
  .btn {
    width: 100%;
    height: 45px;
    background-color: rgb(5, 109, 255);
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 10px;
  }
  
  .btn:hover {
    background-color: #357ab8;
    transform: scale(1.05);
  }
  
  /* Fade-in animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  .errorMessage {
    color: red;
    font-size: 14px;
    margin-top: 10px;
  }
  